import React, { useState } from "react";
import "../../../shared/settings.css";
import { AiFillEdit } from "react-icons/ai";
import { GoLocation } from "react-icons/go";

import AdminSettingsHeader from "../../../components/AdminSettingsHeader";
import Modal from "../../../components/Modal";
import { Button, FormGroup, Input, Table } from "mtforms";
import {
  useAddLocation,
  useDeleteLocation,
  useGetLocation,
  useUpdateLocation,
} from "../hooks";
import { toast } from "react-toastify";
import { errorAlert, toastOptions } from "../../../utils";
import { useIsMutating } from "@tanstack/react-query";
import { location_columns } from "../Report/column";
import swal from "sweetalert";
import Layout from "../../../layout";

const Users = () => {
  const [open, setOpen] = useState(false);
  const [edit, setEdit] = useState(false);

  const [loading, setLoading] = useState(false);
  const [formData, setFormData] = useState({});
  const [errors, setErrors] = useState({});
  const isLoading = useIsMutating();

  const { data } = useGetLocation();

  const { mutate, isError, isSuccess, reset, error } = useUpdateLocation();
  const {
    mutate: addLocation,
    isError: addError,
    isSuccess: isSuccessAdd,
    reset: resetAdd,
    error: errorAdd,
  } = useAddLocation();

  const getLocation = () => {
    if (!navigator.geolocation) {
      setErrors({
        ...errors,
        location: "Geolocation is not supported by your browser",
      });
    } else {
      setLoading(true);
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setFormData({
            ...formData,
            longitude: position.coords.longitude.toString(),
            latitude: position.coords.latitude.toString(),
          });
          setLoading(false);
        },

        () => {
          setErrors({
            ...errors,
            location: "Unable to retrieve your location",
          });
        }
      );
    }
  };

  const actions = (item) => [
    {
      name: "Edit",
      onClick: (res) => {
        setFormData(res);
        setEdit(true);

        setOpen(true);
      },
    },
    {
      name: "Delete Location",
      onClick: (res) => {
        swal({
          title: "Are you sure?",
          text: "Once deleted, you will not be able to recover this",
          icon: "warning",
          //    @ts-ignore
          buttons: true,
          dangerMode: true,
        }).then((willDelete) => {
          if (willDelete) {
            remove(res.id);
          }
        });
      },
    },
  ];

  const currentLocation = () => {
    getLocation();
  };

  const submitHandler = () => {
    if (edit === true) {
      mutate(formData);
    } else {
      addLocation(formData);
    }
  };

  const closeHandler = () => {
    setOpen(false);
    setFormData({});
    setEdit(false);
  };

  if (isError) {
    reset();
    errorAlert(error);
  }
  if (isSuccess) {
    reset();
    setOpen(false);
    setFormData({});
    setEdit(false);
    toast.success(`Location Updated Successfully`, toastOptions);
  }

  if (addError) {
    resetAdd();
    errorAlert(errorAdd);
  }
  if (isSuccessAdd) {
    setFormData({});
    setOpen(false);

    resetAdd();
  }

  const {
    mutate: remove,
    isError: isEDelete,
    isSuccess: isSDelete,
    reset: eReset,
    error: Error,
  } = useDeleteLocation();

  if (isEDelete) {
    eReset();

    errorAlert(Error);
  }
  if (isSDelete) {
    eReset();
    toast.success(`Location Deleted Successfully`, toastOptions);
  }

  const openHandler = () => {
    setOpen(true);
  };
  const handleChange = (name, value) => {
    setFormData({ ...formData, [name]: value });
  };
  const validationHandler = (name, error) => {
    setErrors({ ...errors, [name]: error });
  };

  return (
    <Layout name="Settings" title="Settings">
      <div className="settings_container">
        <div className="settings_container_layout">
          <div className="setting_container">
            <AdminSettingsHeader location={"remote"} />
            <div className="title">
              <span className="ams__icon">
                <GoLocation />
              </span>
              Office Location
              <span className="companyName">
                <AiFillEdit className="ams__icon_btns" onClick={openHandler} />
              </span>
            </div>
            <div className="title">
              <Table
                columns={location_columns}
                data={data}
                actions={actions}
                selectID="id"
                showFilter
              />
            </div>
            <Modal
              isVisible={open}
              title={""}
              size="md"
              content={
                <div className="modal_form_container">
                  {loading ? (
                    "fetching your current location"
                  ) : (
                    <FormGroup
                      validation={formData}
                      errors={errors}
                      setErrors={setErrors}
                      className="Form"
                    >
                      <Input
                        name="name"
                        label="name"
                        value={formData["name"]}
                        onChange={handleChange}
                        type="text"
                        validationHandler={validationHandler}
                        error={errors.name}
                        required={true}
                        readOnly={true}
                        size="large"
                        className="formInput"
                      />
                      <Input
                        name="phone"
                        label="Phone"
                        value={formData["phone"]}
                        onChange={handleChange}
                        type="text"
                        validationHandler={validationHandler}
                        error={errors.phone}
                        required={true}
                        readOnly={true}
                        size="large"
                        className="formInput"
                      />
                      <Input
                        name="latitude"
                        label="Latitude"
                        value={formData["latitude"]}
                        onChange={handleChange}
                        type="text"
                        validationHandler={validationHandler}
                        error={errors.latitude}
                        required={true}
                        readOnly={true}
                        size="large"
                        className="formInput"
                      />

                      <Input
                        name="longitude"
                        label="Longitude"
                        value={formData["longitude"]}
                        onChange={handleChange}
                        type="text"
                        validationHandler={validationHandler}
                        error={errors.longitude}
                        required={true}
                        readOnly={true}
                        size="large"
                        className="formInput"
                      />

                      <Input
                        name="radius"
                        label="Radius"
                        value={formData["radius"]}
                        onChange={handleChange}
                        type="text"
                        validationHandler={validationHandler}
                        error={errors.radius}
                        required={true}
                        readOnly={true}
                        size="large"
                        className="formInput"
                      />

                      <Input
                        name="address"
                        label="address"
                        value={formData["address"]}
                        onChange={handleChange}
                        type="text"
                        validationHandler={validationHandler}
                        error={errors.address}
                        required={true}
                        readOnly={true}
                        size="large"
                        className="formInput"
                      />

                      <Button
                        size="large"
                        type="button"
                        onClick={currentLocation}
                        title={isLoading ? "..loading" : "Use Current Location"}
                        disabled={isLoading ? true : false}
                        className="formButton"
                      />
                      <Button
                        size="large"
                        onClick={submitHandler}
                        title={isLoading ? "submitting" : "Save"}
                        disabled={isLoading ? true : false}
                        className="formButton"
                      />
                    </FormGroup>
                  )}
                </div>
              }
              onClose={closeHandler}
              footer=""
            />
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Users;
